<template>
	<div class="table_list">
		<!--头部搜索-->
		<div class="search">
			<div class="btn">
				<!--名称或编号-->
				<el-select allow-create filterable v-model="no" placeholder="名称或编号">
					<el-option label="全部" value="全部">
					</el-option>
					<el-option v-for="item in  tableData " :label="item.seat_id" :value="item.seat_id" :key="item.seat_id">
					</el-option>

				</el-select>
				<!--餐桌类型-->
				<el-select allow-create filterable v-model="table" placeholder="餐桌类型">
					<el-option label="全部" value="全部">
					</el-option>
					<el-option v-for="item in  tableData " :label="item.seat_type" :value="item.seat_type" :key="item.seat_type">
					</el-option>
				</el-select>
				<!--搜索按钮-->
				<button  class="blue search_button" @click="handleFilter">
					搜索
				</button>
			</div>
			<div class="btn">
				<!--<button @click="suibian">123</button>-->
				<router-link :to="{path:'/table/menu'}">
					<button class="blue">
						订购餐牌
					</button>
				</router-link>
				<!--创建-->
				<router-link :to="{path:'/table/edit'}">
					<button  class="blue create_button">
						新建
					</button>
				</router-link>
				<!--导出二维码-->
				<button class="blue export_button" @click="handleDownload">
						<span v-if="!multipleSelection.length">导出二维码</span>
						<a :href="downLoad" class="downLoad" v-if="multipleSelection.length">导出二维码</a>
				</button>
				<!--删除-->
				<button  class=" red delete_button"@click="deleteDatasInBtnGroup()">
					删除
				</button>
			</div>
		</div>

		<!--列表-->
		<div class="table_info">
			<el-table :data="tableList" @selection-change="selectItem"style="width: 100%" stripe height="550" ref="tableList" @select="selectRow">
				<el-table-column type="selection" width="35">
				</el-table-column>
				<el-table-column prop="seat_id" label="编号" align="center" width="180" sortable  
					:default-sort = "{prop: 'date', order: 'descending'}">
				</el-table-column>
				<el-table-column prop="seat_name" label="餐桌台号" align="center" width="180">
				</el-table-column>
				<el-table-column prop="seat_region" label="餐桌区域"align="center"width="180" >
				</el-table-column>
				<el-table-column prop="seat_type" label="餐桌类型" align="center"width="180">
				</el-table-column>  
				<el-table-column prop="seat_shape" label="桌型" align="center" width="180">
				</el-table-column>
				<el-table-column prop="seat_size" label="可供就餐人数" align="center" width="180" sortable 
					 :default-sort = "{prop: 'date', order: 'descending'}">
				</el-table-column>
				<el-table-column prop="price" label="餐位费" align="center" width="180" sortable 
					 :default-sort = "{prop: 'date', order: 'descending'}">
				</el-table-column>
				<el-table-column  label="二维码" inline-template align="center" width="180" class="code">
					<span class="blue" type="text" size="small" @click="getQR($index,row)"><span>点击预览</span></span>
				</el-table-column>
				<el-table-column  label="操作"  inline-template align="center" width="190">
				<div class="btng">
				<span class="blue" type="text" size="small">
					<router-link :to="{path:'/table/edit',query:{id:row.seat_id}}">
					编辑
					</router-link>
				</span>
				<span class="red" type="text" size="small" @click="confirmDelete(row)">
					删除
				</span>
					
				</div>
				</el-table-column>
			</el-table>
			<!--分页弹框-->
			 <div class="pagination-container">
			 	<el-pagination small @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.page"
			        :page-sizes="[20,30,40,50]" :page-size="listQuery.limit" layout="sizes, prev, pager, next, jumper" :total="total">
			      </el-pagination>
			 </div>
		</div>
		<!--删除信息弹框-->
		<div class="messageBox" v-if="deleted">
			<div class="deleteMessage">
				<div class="messageHeader">删除提醒</div>
				<p class="tableInfor">确定删除餐桌<span>{{deletedNo}}号</span>的信息？</p>
				<div class="btn">
					<el-button @click="confirm" >确定</el-button>
					<el-button @click="cancel">取消</el-button>
				</div>
			</div>
		</div>
		<!--点击预览二维码-->
		<div class="showQR" v-if="showQr">
			<span class="QR"></span>
			<button class="red close_btn" @click="closeQr">关闭</button>
		</div>
	</div>
</template>

<script>
import { apiMethods } from'../api';
import {commonMethods} from '../index'
import { Public } from '@/config/public'
import { ls } from "@/config/pageStore";
export default {
	data() {
		return {
			downLoad:"",//下载链接
			deleted:false,
			deletedNo:"",
			showQr:false,
			no: "",//名称或编号
			table: "",//餐桌类型
			total: 10,//总页数
			tableData: [],//餐桌列表信息
			tableList:[],
			testList:[],
			multipleSelection:[],//勾选的集合（多选）
			selectIndex:{},
			listQuery: {
		        page: 1,
		        limit: 20,
		        sort: '+id'
		      },
		      
		};
	},
	methods: {
			//复选框
			selectItem(val) {
				this.multipleSelection = val;
			},
			//获取选中的列表的id
			getSelectedIds() {
		        let array = []
		        this.multipleSelection.forEach((res) => {
		          array.push(res.seat_id)
		        })
		        return array
		      },
			//单个删除
			confirmDelete(item) {
				this.deleted=true;
				this.deletedNo=item.seat_id;
      		},
      		//点击确定
      		confirm() {
      			this.deleted=false;
  				apiMethods.changeTableData({
	        		seat_delete:1,
	        		seat_id_list:JSON.stringify([this.deletedNo])
	        	},(resp)=>{
			      	setTimeout(() => {
		             apiMethods.shallowRefresh(this.$route.name)
		            }, 50)
      		
	       	 })
      		},
      		selectRow(selection,row){
      			var arr=[];
      			this.tableList.forEach((r,index) => {
      				for (var i=0;i<this.tableList.length;i++){
      					if(r==selection[i]){
//          			
            			arr.push(index)
            		}
      			}
          });
          
           this.selectIndex[this.listQuery.page]=arr
      		
      		},
		    //点击取消
		    cancel() {
		    	this.deleted=false;
		    },
      		//多选删除
      		deleteDatasInBtnGroup() {
      			 if (!this.multipleSelection.length) {
			          alert("你还没有勾选任何选项")
			          return;
		        }
	      		apiMethods.changeTableData({
	      			seat_delete:1,
		        	seat_id_list:JSON.stringify(this.getSelectedIds())
	      		},(resp)=>{
      				setTimeout(() => {
		             apiMethods.shallowRefresh(this.$route.name)
		            }, 50)
	      		})
      		},
			//获取二维码
			getQR(index,row) {
				let shopiin =Public.getShopinfo();
				this.showQr=true
				var img ='<img src="http://www.ob.com:8080/php/img_get.php?get_seat_qrcode=1&shop_id=4&seat_id=4" class="img"/>'
				apiMethods.getQrCode({
					shop_id:shopiin.shopinfo.shop_id,	
					seat_id:row.seat_id 
				},(resp)=>{
					$(".QR").html(img)
					$(".img").css({"position":"absolute",'left':'800px','top':'300px','height':'400px','width':'400px'})
				})
			},
			//点击关闭二维码
			closeQr() {
				this.showQr=false
			},
			//点击搜索
			handleFilter() {
				apiMethods.getTableData({
				get_seat_list:1
			},(resp)=>{
				this.getTotal()
				this.tableList = apiMethods.search(this.no,this.table,resp.data.seatlist).filter((item, index) => index < this.listQuery.limit * this.listQuery.page && index >= this.listQuery.limit * (this.listQuery.page - 1))
				})
			},
			//获取总条目数
			getTotal() {
				 var tot = 0;
			    for (var i = 0 ;i < this.testList.length;i++){
			    	tot++
			    }
			     this.total = tot;
			     return this.total;
			},
			//选择每页显示的条目数
			handleSizeChange(val) {
			     this.listQuery.limit = val;
			     localStorage.setItem("itemNum",this.listQuery.limit)
			     this.tableList=this.testList.filter((item, index) => index < this.listQuery.limit * this.listQuery.page && index >= this.listQuery.limit * (this.listQuery.page - 1))
			     this.getTotal()
			},
			//选择当前页码
			handleCurrentChange(val) {
				this.tableList=this.testList.filter((item, index) => index < this.listQuery.limit * this.listQuery.page && index >= this.listQuery.limit * (this.listQuery.page - 1))
				this.listQuery.page  = val;
				this.getTotal();
				var allSelectIndex=this.selectIndex[val];
				var indexArr=[];
				if(allSelectIndex!==undefined){
					for (var i in this.selectIndex[val]){
						let index =Number(this.selectIndex[val][i]+1)
						$(".el-checkbox__input").eq(index).addClass("is-checked")
					}
				}else{
					
//					$(".el-checkbox__input").eq(0).removeClass("is-checked")
//					$(".el-checkbox__input").eq(1).removeClass("is-checked")
				}
			},
			//点击导出
			handleDownload() {
				if (!this.multipleSelection.length) {
		          alert("你还没有勾选任何选项")
		          return;
	       		}else{
			       	$(".downLoad").attr("qrcode","download")
			      	this.downLoad="http://www.ob.com:8080/php/img_get.php?batch_export_seat_qrcode=1&seat_list="+JSON.stringify(this.getSelectedIds())+"&shop_id='4'"
	        	}  
			},
			
	},
	created() {

			if(localStorage.getItem("itemNum")!=null){
				this.listQuery.limit=Number(localStorage.getItem("itemNum"))
			}
			//初始化表格
			apiMethods.getTableData({
				get_seat_list:1
			},(resp)=>{
				this.tableData = resp.data.seatlist
				this.tableList = resp.data.seatlist
				this.testList = resp.data.seatlist
				this.getTotal()
				this.tableList=this.testList.filter((item, index) => index < this.listQuery.limit * this.listQuery.page && index >= this.listQuery.limit * (this.listQuery.page - 1))
		})
	},
	mounted() {
//		this.getTotal()
	}
}; 
</script>

<style lang="scss" scoped>
@import 'src/styles/mixin.scss';
*{
	padding: 0;
	margin: 0;
}
.table_list {
	height:710px;
	background: #FFFFFF;
	padding: 20px 20px 0 20px;
	
}

.el-select {
	width: 160px;
	height: 34px;
	margin-right: 10px;
}
.search{
	display: flex;
	justify-content: space-between
}
.btn button{
	width: 100px;
	height: 34px;
	border-radius: 3px;
	background: #FFFFFF;
}
.btn .blue {
	margin-right: 10px;
	color: #4877E7;
	border: 1px solid #4877E7;
}
.btn .red {
	margin-right: 0px;
	color: #E7487E ;
	border: 1px solid #E7487E ;
}
.blue{
	font-size: 14px;
	color:  #4877E7;
}

.red {
	font-size: 14px;
	color: #E7487E ;
	
}
.btng span{
	width: 29px;
	height: 19px;
	margin-right: 20px;
	box-sizing: border-box;
}
.table_info {
	padding-top: 20px;
	box-sizing: border-box;
}
.table_info .el-table-column{
	padding: 0;
}
.pagination-container{
	margin: 50px 0 0 689px;
	font-family: MicrosoftYaHei;
	font-size: 12px;
	color: #666666;
	letter-spacing: 0.43px;
}
.pageSize{
	font-size: 12px;
	width: 70px;
	height: 34px;
	margin-right: 10px;
}
.el-table .cell.img{
	width: 60px ;
	height: 60px;
}
div.el-select.el-input{
	font-size: 12px;
}
.jump{
	font-size: 12px;
	margin-right: 10px;
}
.jump .el-input{
	font-size: 12px;
	width: 80px;
	height: 34px;
}
.jump .el-button{
	font-size: 12px;
	width: 30px;
	height: 34px;
	background: #F3F3F3;
}
.messageBox{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background: rgba(0,0,0,0.5);
	z-index: 9993;
}
.deleteMessage{
	z-index: 9995;
	background: #FFFFFF;
	width:540px;
	height: 264px;
	margin: 300px 609px;
}
.messageHeader{
	width: 540px;
	height: 40px;
	background: #5A8CFF;
	color: white;
	font-size: 16px;
	text-align: center;
	line-height: 40px;
}
.tableInfor{
	margin: 69px 130px;
}
.deleteMessage .btn{
	margin-left: 130px;
}
.showQR{
	background: rgba(0,0,0,0.5);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 9990;
}
.close_btn{
	width: 100px;
	height: 34px;
	position: absolute;
	top: 720px;
	left: 950px;
	z-index: 9991;
}
</style>


